<template>
  <div>
    <bet-page v-on:listenChild="selectBet" @up="up" @down="down" :sum="sum" id="bet"></bet-page>
    <div v-show="isShow === true" class="bet-area">
      <div class="left">
        <p>已选中 <strong>{{sum}}</strong> 注</p>
        <el-input v-model.lazy="price" ref="priceInput" id="data1" type="number" placeholder="请输入每注金额"></el-input>
      </div>
      <div class="right">
        <el-button type="primary" class="btn" @click="betSubmit">下注</el-button>
        <el-button type="warning" class="btn" @click="reset">重置</el-button>
      </div>
    </div>
    <div v-show="isShow === false" class="mask">开奖期间不能进行投注</div>
  </div>

</template>

<script type="text/ecmascript-6">
  import BetPage from 'components/bet-page/bet-page'
  import {MessageBox} from 'mint-ui'
  import {getPkData, getLottery, bet} from 'api/bet'
  import Bus from 'common/js/bus'

  export default {
    data () {
      return {
        sum: 0,
        price: '',
        isShow: true,
        betList: [],
        betStr: [],
        type: 1,
        period: 0
      }
    },
    components: {
      BetPage
    },
    created () {
      this._loadData()
    },
    mounted () {
      Bus.$on('isbet', (res) => {
        this.isShow = res
      })
      Bus.$on('tab', () => {
        this.reset()
      })
      Bus.$on('currentPeriod', (period) => {
        console.log('接收旗数改变')
        this.period = period
        console.log('当前期数为' + this.period)
      })
    },
    methods: {
      selectBet (singleBetStr, result, type) {
        return false
      },
      betSubmit () {
        var re = /^[0-9]+$/
        console.log(parseFloat(this.price));
        if (re.test(this.price) && this.sum > 0 && parseFloat(this.price) > 0) {
          var betList = document.getElementById('bet').getElementsByTagName('li')
          for (var i = 0, len = betList.length; i < len; i++) {
            if (betList[i].className === 'select') {
              var dom = betList[i]
              var result = dom.getAttribute('data-result')
              this.type = dom.getAttribute('data-type')
              var rankName = dom.getAttribute('data-rankName')
              var rank = dom.getAttribute('data-rank')
              var menoy = this.price
              var singleBetStr = ''
              if (this.type === '1') {
                singleBetStr = '排名区 - ' + rankName + ':' + dom.innerHTML.substr(0, dom.innerHTML.indexOf('&')) + '<br>'
              } else if (this.type === '2') {
                singleBetStr = '两面区 - ' + rankName + ':' + dom.innerHTML.substr(0, dom.innerHTML.indexOf('&')) + '<br>'
              }
              this.betList.push({
                'money': menoy,
                'rank': rank,
                'result': result
              })
              this.betStr.push(singleBetStr)
            }
          }

          MessageBox.confirm(this.betStr + '<br>' + '每注' + this.price + '元', '投注确认').then(() => {
            if (this.isShow) {
              this._bet({
                'betList': this.betList,
                'period': this.period,
                'type': this.type
              })
              this.reset()
            } else {
              MessageBox.alert('已经到开奖时间 不能投注', '提示')
            }
          }).catch(() => {
            this.reset()
          })
        } else if (this.sum === 0) {
          MessageBox.alert('请选择您的投注', '提示')
          return false
        } else {
          MessageBox.alert('输入金额格式有误', '提示')
          this.price = ''
          return false
        }
      },
      up () {
        this.sum++
      },
      down () {
        this.sum--
      },
      _bet (data) {
        bet(data).then((res) => {
          var result = res.data
          if (result.code === '0') {
            Bus.$emit('changeBalance', result.data.balance)
            Bus.$emit('betSuccess')
            MessageBox.alert(result.msg, '提示')
          } else if (result.code === '500101') {
            MessageBox.alert(result.msg, '提示')
          } else {
            MessageBox.alert(result.msg, '提示')
          }
        }).catch((err) => {
          console.log(err)
        })
      },
      reset () {
        this.sum = 0
        this.betList = []
        this.betStr = []
        this.price = ''
        var aLi = document.getElementById('panel').getElementsByTagName('li')
        for (var i = 0, len = aLi.length; i < len; i++) {
          aLi[i].className = ''
        }
      },
      _loadData () {
        getLottery().then((res) => {
          if (res.data.code === '0') {
            var result = res.data.data
            this.period = result.issue
          } else {
            MessageBox('提示', '读取错误失败')
          }
        })
      }
    }
  }
</script>

<style scope lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .bet-area
    background: #ccc
    width 100%
    height: 80px
    position: fixed
    left: 0
    bottom: 50px
    display: flex
    .left
      width 50%
      flex 1
      p
        line-height: 30px
        strong
          color yellow
          font-weight: bold
    .right
      flex 1
      .btn
        margin-top: 30px
        margin-left: 10px

  .el-message-box
    width: 70%

  .mask
    background: rgba(0, 0, 0, 0.5)
    width 100%
    height: 80px
    position: fixed
    z-index 99
    left: 0
    bottom: 50px
    display: flex
    color red
    font-weight: bold
    line-height: 80px
    text-align: center

</style>


