<template>
  <div class="home">
    <div class="result">
      <h5>第 <strong>{{preIssue}}</strong>期开奖结果 ：</h5>
      <ol class="num" ref="num">
        <li :class="{ 'num-list': true }" v-for="(item, index) in numArr">{{item}}</li>
      </ol>
      <div class="draw draw1">
        <p>龙虎榜</p>
        <div class="draw-list">
          <span v-for="(item, index) in dragonTiger">{{item}}</span>
        </div>
      </div>
      <div class="draw">
        <p>冠亚和</p>
        <div class="draw-list">
          <span v-for="(item, index) in oneTwoSum">{{item}}</span>
        </div>
      </div>
    </div>

    <div class="account" >
      <h3>历史开奖</h3>
      <div class="wrapper" ref="history">
        <div class="content" ref="listView">
          <div class="top-tip">
            <span class="refresh-hook">{{tips}}</span>
          </div>
          <div v-if="isShowAlert" class="alert">刷新成功</div>
          <div v-if="historyList.length">
            <ul class="history-list" id="history">
              <li  v-for="(item, key) in historyList">
                <span class="f2">{{item.openDateTime.substr(5)}}</span>
                <span >{{item.period}}</span>
                <ol class="f3">
                  <span v-for="number in  item.openNums" class="h-item">{{number}}</span>
                </ol>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import {getHistory, getPkData, getLottery} from 'api/bet'
  import Bus from 'common/js/bus'
  import {setCookies, getCookies} from '../../common/js/cookie'
  import {MessageBox} from 'mint-ui'

  export default {
    data () {
      return {
        acc: 'lei',
        article: [],
        count: 1,
        isShowAlert: false,
        tips: '下拉刷新',
        numArr: [],
        dragonTiger: [],
        oneTwoSum: [],
        preIssue: '',
        historyList: [],
        colorArr: ['#E6DE00', '#0092DD', '#4B4B4B', '#FF7600', '#17E2E5', '#5234FF', '#BFBFBF', '#FF2600', '#780B00', '#07BF00']
      }
    },
    created () {
      this._loadData()
      this._loadHistory()
    },
    mounted () {

    },
    methods: {
      _loadData () {
        var that = this
        getLottery().then((res) => {
          var result = res.data.data
          if (res.data.code === '0') {
            that.numArr = result.openNum
            that.dragonTiger = result.dragonTiger
            this.oneTwoSum = result.oneTwoSum
            this.preIssue = result.preIssue
          } else {
            MessageBox('提示', '读取错误失败')
          }
          that._setColor()
        }).catch((err) => {
          console.log(err)
        })
      },
      _refreshAlert () {
        this.isShowAlert = true
        setTimeout(() => {
          this.isShowAlert = false
        }, 1000)
      },
      inPageDetail(key, item) {
        this.$router.push({
          path: `/daily/${key}`
        })
        Bus.$emit('record', item)
      },
      _setColor () {
        this.$nextTick(() => {
          var colorArr = ['#E6DE00', '#0092DD', '#4B4B4B', '#FF7600', '#17E2E5', '#5234FF', '#BFBFBF', '#FF2600', '#780B00', '#07BF00']
          var aOl = this.$refs.num
          var oLi = aOl.getElementsByTagName('li')
          for (var k = 0, len = oLi.length; k < len; k++) {
            var index = oLi[k].innerHTML - 1
            oLi[k].style.background = colorArr[index]
          }
        })
      },
      _setHistroyColor () {
        this.$nextTick(() => {
          var colorArr = ['#E6DE00', '#0092DD', '#4B4B4B', '#FF7600', '#17E2E5', '#5234FF', '#BFBFBF', '#FF2600', '#780B00', '#07BF00']
          var aOl = document.getElementById('history')
          if (aOl) {
            var oLi = aOl.getElementsByClassName('h-item')
            for (var k = 0, len = oLi.length; k < len; k++) {
              var index = oLi[k].innerHTML - 1
              oLi[k].style.background = colorArr[index]
            }
          }
        })
      },
      _loadHistory () {
        getHistory().then((res) => {
          this.historyList = res.data.data
          this.$nextTick(() => {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$refs.history, {probeType: 1, click: true})
              this.scroll.on('touchEnd', (pos) => {
                if (pos.y > 50) {
                  this.tips = '释放立即刷新'
                  var timer = setTimeout(() => {
                    this._loadData()
                    this.tips = '下拉刷新'
                    this._refreshAlert()
                  }, 500)
                }
              })
            } else {
              this.scroll.refresh()
            }
            this._setHistroyColor()
          })
        })
      }
    }
  }
</script>

<style scope lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .result
    width: 100%
    height: 110px
    color: #fff
    position: absolute
    left: 0
    top: 60px
    z-index 99
    box-sizing border-box
    padding: 0px 10px 0
    border-top: 2px solid #fff
    h5
      margin: 0
      padding: 0
      height: 20px
      line-height: 20px
      text-align: left
      font-size $font-size-small-s
      strong
        font-size $font-size-medium
    ol.num
      width: 100%
      height: 35px
      margin: 0
      padding: 0 2%
      display: flex
      box-sizing border-box
      .num-list
        display: inline-block
        float: left
        flex: 1
        line-height: 25px
        margin: 5px
        color #fff
        font-weight: bold
        border-radius: 3px
        background: #D9563D
    .draw
      width: 40%
      float: left
      line-height: 24px
      border: 1px solid #ddd
      .draw-list
        display: flex
        border-top: 1px solid #ddd
        span
          display: inline-block
          flex: 1
          border-right: 1px solid #ddd
          background: rgba(255, 255, 255, .19)
          &:last-child
            border-right: none
    .draw1
      margin-right: 5%
      width: 50%

  .account
    position: relative
    top: 110px
    left: 0
    h3
      height: 28px
      line-height: 28px
      font-size: $font-size-medium-x
      text-align: left
      font-weight: bold
      padding-left: 20px
      border: 1px solid #ddd
    .wrapper
      position: fixed;
      z-index: 1;
      top: 200px;
      bottom: 0;
      left: 0;
      width: 100%;
      overflow: hidden;
      .content
        .alert
          width: 100%
          height: 40px
          line-height: 40px
          position: absolute
          left: 0
          top: 0
          background: rgba(25, 0, 0, 0.5)
          color: #fff
        .top-tip
          position: absolute;
          top: -40px;
          left: 0;
          z-index: 1;
          width: 100%;
          height: 40px;
          line-height: 40px;
          text-align: center;
          color: #555
        .list
          width: 100%
          box-sizing border-box
          border: 1px solid #ddd
          box-shadow #eee 5px 5px inset
          p.title
            font-size: 14px
            text-indent: 2em
            text-align: left
          p.info
            margin: 0
            padding: 0
            line-height: 24px
            font-size: 12px
            text-align: left
            color: #666
            background: #eee
            span
              margin-left: 20px
        .history-list
          li
            line-height: 24px
            padding 3px 10px
            border-bottom: 1px solid #ccc
            font-size: $font-size-medium
            display flex
            span
              flex 1
            .f2
              flex: 2
            .f3
              flex 4
              display flex
              span
                flex 1
                margin 0 2px
                color #ffffff
                font-weight: bold
                border-radius 2px



            
</style>


