<template>
  <div class="contact" id="contact">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="规则明细" name="first">
        <div ref="rule" class="firstPage">
          <div class="content">
            <h3 class="title">规则明细</h3>
            <p v-for="item in ruleArr">{{item.content}}</p>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="联系客服" name="sec">
        <p style="text-align: left;margin-top: 10px;margin-left: 10px;">扫描下面二维码 联系客服</p>
        <div style="margin-top: 20px;">
          <img :src="imgUrl" width="40%" alt="">
        </div>
      </el-tab-pane>
      <el-tab-pane label="充值" name="third">
        <div class="recharge-wrapper">
          <p style="margin-bottom: 10px;padding-top: 10px;">扫描下面二维码 充值成功 会自动更新积分</p>
          <div style="margin-top: 20px;">
            <img :src="imgUrl" width="40%" alt="">
          </div>
        </div>
        <div class="recharge" ref="recharge" style="height: 335px;">
          <div class="recharge-content">
            <ul>
              <li v-for="item in recharge" style="text-align: left; font-size: 12px;line-height: 20px;">
                <span>时间:{{item.createTime}}</span><span style="margin: 10px">操作：{{item.remark}}</span><span>余额：{{item.balance}}</span>
              </li>
            </ul>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="提现" name="four">
        <div class="withcharge-wrapper">
          <p style="text-align: left;padding-top: 10px;">请输入提现金额，提交以后，管理员确认以后系统会自动更新积分。</p>
          <div style="margin-top: 20px;">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item label="提现金额">
                <el-input v-model="money" placeholder="请输入提现金额"></el-input>
              </el-form-item>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="apply()">提交</el-button>
              </el-form-item>
            </el-form>

          </div>
        </div>
        <div class="withdraw" ref="withdraw" style="height: 233px;">
          <div class="withdraw-content">
            <ul>
              <li v-for="item in withdraw" style="text-align: left; font-size: 12px; line-height: 20px;">
                <span>{{item.createTime}}</span><span style="margin: 10px">状态：{{item.statusLabel}}</span><span>金额：{{item.money}}</span>
              </li>
            </ul>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import {getRecharge, getWithdraw, getApply, getImg, getRule} from 'api/bet'
  import {MessageBox} from 'mint-ui'
  import Bus from 'common/js/bus'

  export default {
    data() {
      return {
        activeName: 'first',
        recharge: [],
        withdraw: [],
        money: '',
        imgUrl: '',
        ruleArr: []
      };
    },
    created () {

    },
    mounted () {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.rule, {probeType: 1, click: true, autoBlur: true})
      })
      this._loadData()
    },
    methods: {
      handleClick(tab, event) {
        if (tab.name === 'third') {
          this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.recharge, {probeType: 1, click: true, autoBlur: true})
          })
        } else if (tab.name === 'sec') {
          getImg().then((res) => {
            console.log(res);
            this.imgUrl = res
          })
        } else if (tab.name === 'four') {
          this.$nextTick(() => {
            this.scroll2 = new BScroll(this.$refs.withdraw, {probeType: 1, click: true, autoBlur: true})
          })
        }
      },
      _loadData () {
        getRecharge().then((res) => {
          this.recharge = res.data.data
        })
        getWithdraw().then((res) => {
          this.withdraw = res.data.data
        })
        getRule().then((res) => {
          if (res.data.code === '0') {
            this.ruleArr = res.data.data
          }
        })
      },
      apply () {
        if (this.money) {
          var money = parseFloat(this.money)
          getApply(money).then((res) => {
            if (res.data.code === '0') {
              this.money = ''
              Bus.$emit('tixian', res.data.data)
              MessageBox('提示', res.data.msg)
            } else if (res.data.code === '500400') {
              MessageBox('提示', res.data.msg)
            } else {
              MessageBox('提示', '提现失败 请刷新重试')
            }
          })
        } else {
          MessageBox('提示', '请输入提现金额')
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  #contact
    .el-tabs__nav
      position: relative
      left: 10px
      .el-tabs__item
        color #fff
        &:nth-child(1)
          padding-left: 20px

  .firstPage
    width: 100%
    height 600px
    .content
      width 100%
      padding 5px 10px
      overflow: hidden
      box-sizing border-box
    .title
      color #fff
      margin-bottom: 10px
    p
      width: 100%
      padding 5px 0px
      text-align: left
      line-height: 24px
      box-sizing border-box

  .recharge-wrapper
    position: relative
    z-index 9
    margin-bottom: 10px
    background: -webkit-linear-gradient(90deg, #bf7e83, #813a64);

  .withcharge-wrapper
    position: relative
    z-index 9
    margin-bottom: 10px
    background: -webkit-linear-gradient(90deg, #bf7e83, #813a64);

  .el-form--inline .el-form-item__label
    color #631212
</style>

<style lang="stylus" rel="stylesheet/stylus">
  .el-tabs__header
    margin: 0

</style>




