<template>
  <div class="bet-page-wrapper">
    <el-tabs v-model="activeName" @tab-click="handleClick" id="panel" style="height: 100%;">
      <el-tab-pane label="  排名区 投注  " name="first">
        <div class="wrapper1" ref="wrapper1" @click="test">
          <div class="content1">
            <div class="bet-wrapper" v-for="(item, index) in betData">
              <p class="title">{{item.rankName}}</p>
              <ul class="list">
                <li @click="toggle(content.result, $event)" v-for="(content, secondIndex) in item.data"
                    :data-result="content.result" :data-type=1 :data-rankName="item.rankName" :data-rank="content.rank">{{content.resultName}} &nbsp; {{content.rate}}</li>
              </ul>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="  两面反 投注  " name="second">
        <div class="wrapper1" ref="wrapper2">
          <div class="content1">
            <div class="bet-wrapper" v-for="(item, index) in betData2">
              <p class="title">{{item.rankName}}</p>
              <ul class="list">
                <li @click="toggle(content.result, $event)"  v-for="(content, secondIndex) in item.data"
                    :data-result="content.result" :data-type=2 :data-rankName="item.rankName" :data-rank="content.rank">{{content.resultName}} &nbsp; {{content.rate}}</li>
                <!--     <li><span class="name" >冠亚小</span> <span class="odds">1.98</span></li>
                     <li><span class="name" >冠亚单</span> <span class="odds">1.98</span></li>
                     <li><span class="name" >冠亚双</span> <span class="odds">1.98</span></li>-->
              </ul>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>

</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import {getRuleNumber, getRuleSide} from 'api/bet'
  import Bus from 'common/js/bus'
  export default {
    props: {
      sum: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        activeName: 'first',
        counter: 0,
        data1: '',
        betData: [],
        betData2: []
      };
    },
    created () {
      this._loadData()
    },
    mounted () {
      console.log(2332)
    },
    methods: {
      handleClick(tab, event) {
        Bus.$emit('tab')
        this.$nextTick(() => {
          this.scroll2 = new BScroll(this.$refs.wrapper2, {probeType: 1, click: true, autoBlur: true})
        })
      },
      betPage1 () {
        console.log(22)
        this.$nextTick(() => {
          this.scroll = new BScroll(this.$refs.wrapper1, {probeType: 1, click: true, autoBlur: true})
        })
      },
      toggle (result, e) {
        var dom = e.target
        var type = dom.getAttribute('data-type')
        var htmlStr = ''
        if (dom.className === 'select') {
          dom.className = ''
          this.$emit('down')
          htmlStr = ''
        } else {
          dom.className = 'select'
          this.$emit('up')
          htmlStr = dom.innerHTML
          var subLimit = htmlStr.indexOf('&')
          var singleBetStr = htmlStr.substr(0, subLimit)
        }
        this.$emit('listenChild', singleBetStr, result, type)
      },
      _loadData () {
        getRuleNumber().then((res) => {
          this.betData = res.data
          this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.wrapper1, {probeType: 1, click: true, autoBlur: true})
          })
        })
        getRuleSide().then((res) => {
          this.betData2 = res.data
        })
      },
      test () {
        this.$nextTick(() => {
          document.getElementById('data1').blur();
        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .bet-page-wrapper
    position: absolute
    top 61px
    bottom 72px
    left: 0
    right: 0
    .el-tabs__header
      margin 0
      height 40px
    .el-tabs__content
      height 90%
    .el-tabs__nav
      margin-left: 40px
      font-weight: 900
      .el-tabs__item
        color #ffffff

    .bet-wrapper
      width: 100%
      margin-bottom: 5px
      box-sizing border-box
      padding 0 10px 8px
      line-height: 36px
      font-size: 16px
      color #fff
      background: rgba(0, 0, 0, 0.2)
      border-bottom: 1px solid #000
      .title
        cursor none
      .list
        width 100%
        display: flex
        flex-wrap wrap
        li
          width 32%
          box-sizing border-box
          border: 1px solid #ddd
          border-radius 5px
          cursor pointer
          margin 0 2px
          &.select
            background: rgba(255, 0, 0, 0.2)
            color: #ffef42
          .odds
            font-size: 12px

    .wrapper1
      width 100%
      height 300px
      padding-bottom: 80px
</style>




