<template>
  <div class="m-header">
    <div class="info">
      <div class="left">
        <div>距 <span class="issue">{{period}}</span>期开奖仅</div>
        <div v-show="betFlag === true" class="time"><span>{{minute}}</span>分<span>{{second}}</span>秒</div>
        <div v-show="betFlag === false" class="time">开奖中,敬请期待...</div>
      </div>
      <div class="right">
        <div class="username"><span style="float: left;margin-left: 10px;">{{username}}</span>
          <el-button type="primary" @click="logout" style="float: right;margin: 5px 5px 0 0;padding: 5px 6px">退出
          </el-button>
          <el-button type="primary" @click="refresh" style="float: right;margin: 5px 5px 0 0;padding: 5px 6px">刷新
          </el-button>
        </div>
        <div><span style="float: left;">积分 :</span><span class="money">{{balance}}</span></div>
      </div>
    </div>
  </div>
</template>

<script>
  import {setCookies, getCookies, delCookie} from '../../common/js/cookie'
  import {logout} from 'api/user'
  import {getLottery, getBalance} from 'api/bet'
  import {MessageBox} from 'mint-ui'
  import Bus from 'common/js/bus'

  export default {
    data () {
      return {
        username: '',
        period: '',
        openDateTime: '',
        minute: '-',
        second: '-',
        betFlag: true,
        timer: null,
        balance: 0
      }
    },
    created () {
      this._loadData()
    },
    mounted () {
      this.username = localStorage.username
      getBalance(this.username).then((res) => {
        this.balance = res.data.data.balance
      })
      Bus.$on('changeBalance', (balance) => {
        this.balance = balance
      })
      Bus.$on('tixian', (res) => {
        this.balance = res
      })
    },
    methods: {
      _loadData () {
        getLottery().then((res) => {
          if (res.data.code === '0') {
            var result = res.data.data
            this.period = result.issue
            this.openDateTime = result.openDateTime
            this._countDown()
          } else {
            MessageBox('提示', '读取错误失败')
          }
        })
      },
      _countDown () {
        if (!this.openDateTime) {
          MessageBox('提示', '请手动刷新重试')
          return false
        }
        var that = this
        that.timer = setInterval(function () {
          if (!that._checkTime()) {
            that.minute = '-'
            that.second = '-'
            return false
          }
          var currentDate = parseInt(Date.parse(new Date()) / 1000);
          var openTime = that.openDateTime.replace(/-/g, '/');
          var openDate = parseInt(Date.parse(new Date(openTime)) / 1000);
          var remain = openDate - currentDate
          that.minute = parseInt(remain / 60)
          that.second = parseInt(remain % 60)
          if (that.minute < 1 && that.second < 5) {
            that.betFlag = false
            Bus.$emit('isbet', that.betFlag)
            if (that.second < -46) {
              that.minute = '-'
              that.second = '-'
              console.log('重新去获取系统时间')
              clearInterval(that.timer)
              that._refreshData(that)
            }
          }
        }, 1000)
      },
      _refreshData (that) {
        getLottery().then((res) => {
          if (res.data.code === '0') {
            console.log('刷新数据成功')
            var result = res.data.data
            that.period = result.issue
            that.openDateTime = result.openDateTime
            that.betFlag = true
            Bus.$emit('isbet', that.betFlag)
            Bus.$emit('currentPeriod', that.period)
            that._countDown()
          } else {
            MessageBox('提示', '读取错误失败')
          }
        })
      },
      _checkTime () {
        var ar = ['9:07', '23:00']
        var d = new Date()
        var current = d.getHours() * 60 + d.getMinutes()
        var arbegin = ar[0].split(':')
        var arend = ar[1].split(':')
        var b = parseInt(arbegin[0]) * 60 + parseInt(arbegin[1])
        var e = parseInt(arend[0]) * 60 + parseInt(arend[1])
        if (current >= b && current <= e) return true
        else return false
      },
      logout () {
        Bus.$emit('logout', true)
        logout().then((res) => {
          if (res.data.code === '0') {
            console.log('退出登录');
            window.localStorage.isAutoLogout = 'no'
            this.$router.push('/')
          }
        })
      },
      refresh () {
        window.location.reload(true)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .m-header
    width: 100%
    position: relative
    left: 0
    top: 0
    z-index: 3
    height: 60px
    line-height: 30px
    box-sizing: border-box
    padding-left: 10px
    font-size: 12
    color: $color-text
    .info
      width: 100%
      box-sizing border-box
      .left
        float: left
        height: 60px
        width: 42%
        font-size: 12px
        text-align: left
        .issue
          font-size: $font-size-large
        .time
          span
            display: inline-block
            width: 30px
            height: 24px
            line-height: 24px
            margin-bottom: 5px
            text-align: center
            color: $color-text
            background: #333
            margin: 0 10px
      .right
        float: left
        width: 58%
        height: 60px
        .username
          height: 30px
        .money
          font-size: $font-size-large
          margin: 0 10px
          float: left


</style>


