<template>
  <div>
    <div class="wrapper" ref="wrapper">
      <div class="content" ref="listView">
        <div class="top-tip">
          <span class="refresh-hook">{{tips}}</span>
        </div>
        <div v-if="isShowAlert" class="alert">刷新成功</div>
        <div v-if="recordList.length">
          <div class="list" @click="inPageDetail(key, item)" v-for="(item, key) in recordList">
            <p><span class="f2">时间：{{item.createTime}}</span> <span>交易类型：{{item.typeLabel}}</span></p>
            <p><span class="f2">期数：{{item.period == null ? '-' : item.period}}</span> <span>金额：{{item.money}}</span></p>
            <p><span class="f2">余额：{{item.balance}}</span> <span v-if="item.typeLabel === '投注'">详情 <button
                style="background: #2b97f7;border: none;color: #ffffff;border-radius: 5px"
                @click.stop.prevent="showDetail(item.betTime,item.period)">查看投注</button></span><span v-if="item.typeLabel === '开奖结算'">详情 <button
                style="background: #2b97f7;border: none;color: #ffffff;border-radius: 5px"
                @click.stop.prevent="showLotteryDetail(item.betTime,item.period, item.money)">查看开奖</button></span></p>
          </div>
        </div>
        <div v-if="!recordList.length"><h3 style="padding-top: 15px;font-weight: bold;">暂无数据</h3></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import {getRecord, getBetDetail} from 'api/bet'
  import Bus from 'common/js/bus'
  import {MessageBox} from 'mint-ui'

  export default {
    data () {
      return {
        acc: 'lei',
        recordList: [],
        count: 1,
        tips: '下拉刷新',
        isShowAlert: false
      }
    },
    created () {
      this._loadData()
    },
    mounted () {
      Bus.$on('betSuccess', function () {
        this._loadData()
      })
    },
    methods: {
      _loadData () {
        getRecord().then((res) => {
          this.recordList = res.data.data
          this.$nextTick(() => {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$refs.wrapper, {probeType: 1, click: true})
              this.scroll.on('touchEnd', (pos) => {
                if (pos.y > 50) {
                  this.tips = '释放立即刷新'
                  var timer = setTimeout(() => {
                    this._loadData()
                    this.tips = '下拉刷新'
                    this._refreshAlert()
                  }, 500)
                }
              })
            } else {
              this.scroll.refresh()
            }
          })
        })
      },
      _refreshAlert () {
        this.isShowAlert = true
        setTimeout(() => {
          this.isShowAlert = false
        }, 1000)
      },
      _getBetDetail (params) {
        getBetDetail(params).then((res) => {
          if (res.data.code === '0') {
            var betDetailArr = res.data.data
            var betDetailStr = ''
            for (var i = 0, len = betDetailArr.length; i < len; i++) {
              betDetailStr = betDetailStr + betDetailArr[i].rankName + '-' + betDetailArr[i].resultLabel + '， 赔率' + betDetailArr[i].rate + '， 每注金额' + betDetailArr[i].money + '<br>'
            }
            MessageBox('投注详情', betDetailStr)
          }
        })
      },
      _getLotteryDetail (params, money) {
        getBetDetail(params).then((res) => {
          if (res.data.code === '0') {
            var betDetailArr = res.data.data
            var betDetailStr = ''
            for (var i = 0, len = betDetailArr.length; i < len; i++) {
              betDetailStr = betDetailStr + betDetailArr[i].rankName + '-' + betDetailArr[i].resultLabel + ',赔率' + betDetailArr[i].rate + ',金额' + betDetailArr[i].money + '，结算' + betDetailArr[i].bonus + '<br>'
            }
            betDetailStr = betDetailStr + '奖金总结算' + money + '<br>'
            MessageBox('开奖详情', betDetailStr)
          }
        })
      },
      showDetail (betTime, period) {
        var params = {
          'betTime': betTime,
          'period': period
        }
        this._getBetDetail(params)
      },
      showLotteryDetail (betTime, period, money) {
        var params = {
          'betTime': betTime,
          'period': period
        }
        this._getLotteryDetail(params, money)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .wrapper
    position: fixed;
    z-index: 1;
    top: 60px;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #fff;
    overflow: hidden;
    .content
      background: #fff
      min-height 95%
      .alert
        width: 100%
        height: 40px
        line-height: 40px
        position: absolute
        left: 0
        top: 0
        background: rgba(25, 0, 0, 0.5)
        color: #fff
      .top-tip
        position: absolute;
        top: -40px;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #555
      .list
        width: 100%
        box-sizing border-box
        padding 10px
        border: 1px solid #ddd
        height: 75px
        font-size: 12px
        text-align left
        p
          line-height: 18px
          display: flex
          span
            flex 1
            margin-left: 10px
            &.f2
              flex 2
</style>



