<template>
  <div>
    <div class="login-wrap" v-show="showLogin">
      <img src="../../common/img/avatar.gif" class="avatar">
      <h3>登 录</h3>
      <p v-if="showTishi">{{tishi}}</p>
      <input type="text" placeholder="请输入用户名" v-model="account">
      <input type="password" placeholder="请输入密码" v-model="password">
      <button type="button" @click.stop="login">登录</button>
      <span @click="ToRegister">没有账号？马上注册</span>
    </div>

    <div class="register-wrap" v-show="showRegister">
      <img src="../../common/img/avatar.gif" class="avatar">
      <h3>注册</h3>
      <p v-if="showTishi">{{tishi}}</p>
      <input type="text" placeholder="请输入注册手机号" v-model="newAccount">
      <input type="password" placeholder="请输入密码" v-model="newPassword">
      <input type="password" placeholder="请输入确认密码" v-model="comfirmPassword">
      <button type="button" @click.stop="register">注册</button>
      <span @click="ToLogin">已有账号？马上登录</span>
    </div>
  </div>
</template>


<script>
  import {createUser, checkAccount, autoLogin, logout} from 'api/user'
  import {setCookies, getCookies} from '../../common/js/cookie'
  import {MessageBox} from 'mint-ui'
  import Bus from 'common/js/bus'

  export default{
    data () {
      return {
        showLogin: true,
        showRegister: false,
        showTishi: false,
        tishi: '登录成功',
        account: '',
        password: '',
        newAccount: '',
        newPassword: '',
        comfirmPassword: ''
      }
    },
    created () {
      this.isWeiXin()
    },
    mounted () {
      var that = this
      this.account = localStorage.username
      this.password = localStorage.password
      console.log('isAutoLogout=' + localStorage.isAutoLogout)
      if (localStorage.isAutoLogout === 'no') {
        return false
      } else {
        this._autoLogin()
      }
    },
    methods: {
      ToRegister () {
        this.tishi = ''
        this.showLogin = false
        this.showRegister = true
      },
      ToLogin () {
        this.showRegister = false
        this.showLogin = true
      },
      register () {
        var phoneReg = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/
        if (!phoneReg.test(this.newAccount)) {
          MessageBox('提示', '您输入的手机号有误 请重新输入')
          this.newAccount = ''
          return false
        } else if (this.newPassword.length < 6) {
          MessageBox('提示', '密码长度必须大于6位')
          this.newPassword = ''
          return false
        } else if (this.newPassword !== this.comfirmPassword) {
          MessageBox('提示', '密码确认不一致')
          this.comfirmPassword = ''
          return false
        }
        let params = {
          mobile: this.newAccount,
          password: this.newPassword
        }
        createUser(params).then((res) => {
          console.log(res.data)
          if (res.data.code === '500202') {
            MessageBox('提示', res.data.msg)
          } else if (res.data.code === '0') {
            MessageBox('注册成功', '将切换到登录')
            this.showLogin = true
            this.showRegister = false
          }
        }).catch((err) => {
          console.log(err)
        })
        return false
      },
      login () {
        let user = {
          mobile: this.account,
          password: this.password
        }
        if (!this.account) {
          MessageBox('提示', '请填写手机号')
          return false
        }
        if (!this.password) {
          MessageBox('提示', '请填写密码')
          return false
        }
        checkAccount(user).then((res) => {
          var result = res.data.code
          if (result === '0') {
            this.showTishi = true
            this.tishi = '登录成功'
            setCookies('username', this.account)
            localStorage.isAutoLogout = 'yes'
            localStorage.username = this.account
            setTimeout(function () {
              this.$router.push('/home')
            }.bind(this), 100)
          } else {
            this.account = ''
            this.password = ''
            this.showTishi = true
            this.tishi = res.data.msg
          }
        })
      },
      isWeiXin () {
        var ua = window.navigator.userAgent.toLowerCase()
        if (ua.match(/MicroMessenger/i) === 'micromessenger') {
          return true;
        } else {
          console.log('不是微信平台')
          return false;
        }
      },
      _autoLogin () {
        autoLogin().then((res) => {
          if (res.data.code === '0') {
            setCookies('username', this.account)
            localStorage.username = this.account
            localStorage.isAutoLogout = 'yes'
            this.$router.push('/home')
          }
        })
      }
    }
  }
</script>


<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .login-wrap, .register-wrap
    text-align: center
    img
      width: 100px
      height: 100px
      margin 60px 0 30px
      border-radius: 500px
    input
      display: block
      width: 250px
      height: 40px
      line-height: 40px
      margin: 0 auto 10px
      outline: none
      border: 1px solid #888
      padding: 10px
      box-sizing: border-box
    h3
      font-size: $font-size-large
      margin 10px 0
    p
      color: red
    button
      display: block
      width: 250px
      height: 40px
      line-height: 40px
      margin: 0 auto 15px
      border: none
      background-color: #41b883
      color: #fff
      font-size: $font-size-medium-x
    span
      cursor: pointer
      padding-top: 20px
      &:hover
        color: #41b883
    .tap
      margin-top: 20px
</style>


